import React from "react";
import { Recorder } from "react-voice-recorder";
import "react-voice-recorder/dist/index.css";
import "./styles.css";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      uploadStatus: null // 'success', 'fail', or null
    };
  }

  handleAudioStop(data) {
    this.sendToN8N(data.blob);
  }

  async sendToN8N(blobData) {
    const formData = new FormData();
    formData.append("data", blobData);

    try {
      await fetch(
        "https://lupineleads.app.n8n.cloud/webhook/31c5bf5e-ac10-4b0f-bd24-ae0ed6ef17e6",
        {
          method: "POST",
          body: formData
        }
      );
      this.setState({ uploadStatus: "success" });
    } catch (error) {
      console.error("Error:", error);
      this.setState({ uploadStatus: "fail" });
    }
  }

  render() {
    return (
      <div className="App">
        <Recorder
          record={true}
          handleAudioStop={(data) => this.handleAudioStop(data)}
        />
        {this.state.uploadStatus === "success" && <p>Uploaded successfully!</p>}
        {this.state.uploadStatus === "fail" && (
          <p>Error uploading. Try again.</p>
        )}
      </div>
    );
  }
}
